import styled from "styled-components";

import Button from "../../_partials/Button";
import { IItems } from "../../Items";
import { APP_THEME } from "../../../theme";
import Store from "../../../stores/Cart";

const ItemBox = styled.div`
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 6pt 0pt;
  margin: 4pt 0pt;

  border-radius: 8pt;

  background: rgb(18, 18, 18);
`;

const ItemDetails = styled.div`
  width: 75%;
  margin: 0pt 4pt;
`;

const ItemName = styled.p`
  margin: 0;
  padding: 0;

  color: rgb(200, 200, 200);

  font-family: "Roboto+Condensed", sans-serif;
  font-size: 14pt;
`;

const ItemPrice = styled.p`
  margin: 0;
  padding: 0;

  color: rgb(120, 120, 120);

  font-family: "Roboto+Condensed", sans-serif;
  font-size: 8pt;
`;

// Actions

const ItemActions = styled.div`
  display: flex;
  justify-content: right;
  align-items: center;
`;

const Item = ({ name, label, count }: IItems) => {
  return (
    <ItemBox>
      <ItemDetails>
        <ItemName>{label}</ItemName>
        <ItemPrice>
          ${" "}
          {(
            (Store.getState().items.find((x) => x.name === name)?.price || 1) *
            count
          )
            .toFixed(2)
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}{" "}
          ({count} darab)
        </ItemPrice>
      </ItemDetails>

      <ItemActions>
        <Button
          {...APP_THEME.cart.list.buttons.delete}
          onClick={() => Store.dispatch({ type: "REMOVE_CART_ITEM", name })}
        />
      </ItemActions>
    </ItemBox>
  );
};

export default Item;
