import { configureStore } from "@reduxjs/toolkit";
import { Reducer } from "redux";

import reducer, { StoreAction } from "../reducers/Cart";

export interface ICartItem {
  count: number;
  name: string;
}

export interface IShopItems {
  name: string;
  label: string;
  description: string;
  price: number;
  count: number;
}

export interface IStore {
  cart: ICartItem[];
  items: IShopItems[];
  visible: boolean;
}

const Store = configureStore({
  reducer: reducer as Reducer<IStore, StoreAction>,
  preloadedState: {
    cart: [],
    items: [],
    visible: false,
  },
});

export default Store;
