import { BaseSyntheticEvent, useState } from "react";
import styled from "styled-components";
import Store from "../../../stores/Cart";

import { APP_THEME } from "../../../theme";
import { IItems } from "../../Items";
import Button from "../../_partials/Button";

const BarBox = styled.div`
  width: 100%;

  display: flex;
  justify-content: right;
  align-items: center;

  box-sizing: border-box;
  border-radius: ${APP_THEME.list.item.radius};
  padding: ${APP_THEME.list.item.padding};

  background: rgb(26, 26, 26);

  margin-bottom: 4pt;
`;

const ItemIcon = styled.img`
  width: 48px;
  height: 48px;

  margin-right: 6px;
  border-radius: 6pt;

  background: rgb(22, 22, 22);
`;

const ItemNameAndDesc = styled.div`
    width: 40%;
  `,
  ItemName = styled.p`
    margin: 0;
    padding: 0;

    color: rgb(200, 200, 200);

    font-family: "Roboto+Condensed", sans-serif;
    font-size: 14pt;
  `,
  ItemDescription = styled.p`
    margin: 0;
    padding: 0;

    color: rgb(120, 120, 120);

    font-family: "Roboto+Condensed", sans-serif;
    font-size: 8pt;
  `;

const ItemPrice = styled.div`
    width: 22.5%;
    display: flex;
    justify-content: center;
    align-items: center;
  `,
  ItemPriceLabel = styled.span`
    display: block;

    word-wrap: break-word;

    color: rgb(120, 120, 120);

    font-family: "Roboto+Condensed", sans-serif;
    font-size: 8pt;

    &:before {
      content: "\n";
    }
  `;

const ItemActions = styled.div`
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ItemCountBox = styled.div`
    width: 20%;

    display: flex;
    justify-content: center;
    align-items: center;
  `,
  ItemCountText = styled.p``,
  ItemCountReducer = styled.p<{ type: "minus" | "plus" }>`
    margin: 0pt 8pt;
    padding: 0;

    width: 32px;
    height: 32px;

    display: flex;
    justify-content: center;
    align-items: center;

    color: rgba(255, 255, 255, 0.7);

    border-radius: 6pt;
    background: ${(x) =>
      x.type === "plus" ? "rgba(0, 255, 0, .2)" : "rgba(255, 0, 0, .2)"};

    font-size: 10pt;
    font-weight: bold;

    transition: background 100ms linear;

    &::before {
      font-family: "FontAwesome";
      font-weight: bold;
      content: "\\${(x) => (x.type === "plus" ? "f067" : "f068")}";
    }

    &:hover {
      cursor: pointer;
      background: ${(x) =>
        x.type === "plus" ? "rgba(0, 255, 0, .6)" : "rgba(255, 0, 0, .6)"};
    }
  `;

function BarComponent({ name, count, price, description }: IItems) {
  const [amount, setAmount] = useState(1);

  const addItemToCart = (
    e: BaseSyntheticEvent,
    item: { name: string; count: number }
  ) => {
    Store.dispatch({ type: "ADD_CART_ITEM", item: { ...item } });
  };

  return (
    <BarBox>
      <ItemIcon src={`/items/${name}.png`} />
      <ItemNameAndDesc>
        <ItemName>
          {Store.getState().items.find((y) => y.name === name)?.label ||
            "Ismeretlen"}
        </ItemName>

        <ItemDescription>
          {typeof description == "string" &&
          description?.length > APP_THEME.list.item.description.max_length
            ? `${description?.substring(
                0,
                APP_THEME.list.item.description.max_length
              )}...`
            : description}
        </ItemDescription>
      </ItemNameAndDesc>

      <ItemPrice>
        $ {price?.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}
        <ItemPriceLabel>/ darab</ItemPriceLabel>
      </ItemPrice>

      <ItemCountBox>
        <ItemCountReducer
          type="minus"
          onClick={() => {
            if (amount > 1) setAmount(amount - 1);
          }}
        />
        <ItemCountText>{amount}</ItemCountText>
        <ItemCountReducer
          type="plus"
          onClick={() => {
            setAmount(amount + 1);
          }}
        />
      </ItemCountBox>

      <ItemActions>
        <Button
          {...APP_THEME.list.item.buttons.in_cart}
          onClick={(e: BaseSyntheticEvent) =>
            addItemToCart(e, { name, count: amount })
          }
        />
      </ItemActions>
    </BarBox>
  );
}

export default BarComponent;
