import { useEffect, useState } from "react";
import styled from "styled-components";

import { APP_THEME } from "../../theme";
import { IItems } from "../Items";
import BarComponent from "./elements/Bar";

import Store from "../../stores/Cart";

const ListStyle = styled.div`
  width: ${APP_THEME.list.width}%;
  height: 100%;

  overflow-x: none;
  overflow-y: auto;

  box-sizing: border-box;
  padding: 6pt 6pt;

  border-top-left-radius: ${APP_THEME.radius};
  border-bottom-left-radius: ${APP_THEME.radius};

  background: rgb(18, 18, 18);
`;

function ListComponent() {
  const [list, setList] = useState<IItems[]>(Store.getState().items);

  useEffect(() => {
    Store.subscribe(() => setList(Store.getState().items));
  }, []);

  return (
    <ListStyle>
      {list && list.map((x, i) => <BarComponent key={`bar-${i}`} {...x} />)}
    </ListStyle>
  );
}

export default ListComponent;
