import { ICartItem, IShopItems, IStore } from "../stores/Cart";

export type StoreAction =
  | { type: "ADD_CART_ITEM"; item: ICartItem }
  | { type: "REMOVE_CART_ITEM"; name: string }
  | { type: "TUNCATE_CART" }
  | { type: "SET_STATE"; state: boolean }
  | { type: "SET_ITEMS"; items: IShopItems[] };

const Reducer = (state: IStore, action: StoreAction): IStore => {
  switch (action.type) {
    case "ADD_CART_ITEM":
      const hasAlreadyInCart = state.cart.find(
        (x) => x.name === action.item.name
      );

      if (hasAlreadyInCart) {
        return {
          ...state,
          cart: [
            ...state.cart.map((x) =>
              x.name === action.item.name
                ? { ...x, count: x.count + action.item.count }
                : { ...x }
            ),
          ],
        };
      } else {
        return { ...state, cart: [...state.cart, action.item] };
      }
    case "REMOVE_CART_ITEM":
      return {
        ...state,
        cart: [...state.cart.filter((x) => x.name !== action.name)],
      };

    case "TUNCATE_CART":
      return {
        ...state,
        cart: [],
      };

    case "SET_STATE":
      return {
        ...state,
        visible: action.state,
      };

    case "SET_ITEMS":
      return { ...state, items: action.items };

    default:
      return { ...state };
  }
};

export default Reducer;
