import { useEffect, useState } from "react";
import styled from "styled-components";
import Store, { ICartItem } from "../../stores/Cart";

import { APP_THEME } from "../../theme";
import Button from "./elements/Buttons";
import Item from "./elements/Item";

const CartStyle = styled.div`
  width: ${100 - APP_THEME.list.width}%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  border-top-right-radius: ${APP_THEME.radius};
  border-bottom-right-radius: ${APP_THEME.radius};

  background: rgb(22, 22, 22);
`;

const CartElement = styled.div<{ type: "items" | "actions" }>`
  width: 100%;
  height: ${(x) =>
    x.type === "items"
      ? `${APP_THEME.cart.list.height}%`
      : `${100 - APP_THEME.cart.list.height}%`};

  overflow-x: none;
  overflow-y: auto;

  box-sizing: border-box;
  padding: ${(x) => (x.type === "actions" ? "4pt" : "5pt")};
`;

function CartComponent() {
  const [items, setItems] = useState([] as ICartItem[]);

  useEffect(() => {
    Store.subscribe(() => setItems(Store.getState().cart));
  }, []);

  const purchase = () => {
    fetch("http://sShops/purchase", {
      method: "POST",
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        items: Store.getState().cart,
      }),
    });
  };

  return (
    <CartStyle>
      <CartElement type="items">
        {items &&
          items.map((x, i) => (
            <Item
              key={`item-${i}`}
              name={x.name}
              count={x.count}
              label={
                Store.getState().items.find((y) => y.name === x.name)?.label ||
                "Ismeretlen"
              }
            />
          ))}
      </CartElement>
      <CartElement type="actions">
        <Button currentStyle="success" onClick={purchase}>
          {`Vásárlás ($${items
            .reduce(
              (prev, x) =>
                (prev +=
                  x.count *
                  (Store.getState().items.find((y) => y.name === x.name)
                    ?.price || 1)),
              0
            )
            .toFixed(2)
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")})`}
        </Button>
        <Button
          currentStyle="error"
          onClick={() => Store.dispatch({ type: "TUNCATE_CART" })}
        >
          Összes törlése
        </Button>
      </CartElement>
    </CartStyle>
  );
}

export default CartComponent;
