import { useEffect, useState } from "react";
import styled from "styled-components";

import ListComponent from "./list/List";
import CartComponent from "./cart/Cart";
import { APP_THEME } from "../theme";

import Store, { IShopItems } from "../stores/Cart";

const AppStyle = styled.div`
  width: ${APP_THEME.width};
  height: ${APP_THEME.height};

  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;

  color: white;
`;

type INuiMessage =
  | { action: "OPEN"; items: IShopItems[] }
  | { action: "CLOSE" };

function EventListener() {
  window.addEventListener("message", (e) => {
    const { action } = e.data as INuiMessage;

    switch (action) {
      case "OPEN":
        Store.dispatch({ type: "SET_ITEMS", items: [] });
        Store.dispatch({ type: "SET_ITEMS", items: e.data.items });
        Store.dispatch({ type: "SET_STATE", state: true });

        break;

      case "CLOSE":
        Store.dispatch({ type: "SET_STATE", state: false });
        break;
    }
  });

  return null;
}

function OnKeyDown() {
  window.onkeydown = (e: KeyboardEvent) => {
    if (e.key === "Escape") {
      fetch(`http://sShops/close`, {
        method: "POST",
        headers: {
          "Content-type": "application/json; charset=UTF-8",
        },
      });
    }
  };

  return null;
}

console.log(process.env.PUBLIC_URL);

function App() {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    Store.subscribe(() => setVisible(Store.getState().visible));
  }, []);

  return (
    <>
      {visible && (
        <AppStyle>
          <ListComponent />
          <CartComponent />
        </AppStyle>
      )}

      <EventListener />
      <OnKeyDown />
    </>
  );
}

export default App;
